.footer-container {
  background-color: #242424;
  padding: 4rem 0 2rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.footer-subscription {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  margin-bottom: 24px;
  padding: 24px;
  color: #fff;
}
.footer-subscription-heading {
  margin-bottom: 24px;
  font-size: 24px;
}
.footer-subscription-text {
  margin-bottom: 24px;
  font-size: 20px;
}
.footer-input {
  padding: 8px 20px;
  border-radius: 6px;
  outline: none;
  border: none;
  font-size: 18px;
  margin-bottom: 16px;
  border: 1px solid #fff;
}
.footer-input::placeholder {
  color: #b1b1b1;
}
.footer-links {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-link-wrapper {
  display: flex;
}
.footer-link-items {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 16px;
  text-align: left;
  width: 160px;
  box-sizing: border-box;
}
.footer-link-items h2 {
  margin-bottom: 16px;
}
.footer-link-items > h2 {
  color: #fff;
}
.footer-link-items a {
  color: #fff;
  text-decoration: none;
  margin-bottom: 8px;
}
.footer-link-items a:hover {
  color: #e9e9e9;
  text-decoration: underline;
  transition: all 0.32 ease-out;
}
.social-icon-link {
  color: #fff;
  font-style: 24px;
}
.social-media {
  max-width: 1000px;
  width: 100%;
}
.social-media-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-width: 1000px;
  margin: 40px auto 0 auto;
}
.social-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  font-size: 34px;
}
.footer-logo {
  font-size: 24px;
}
.social-logo {
  display: flex;
  gap: 10px;
  align-items: center;
  color: #fff;
  text-decoration: none;
}
.website-right {
  color: #fff;
  font-size: 18px;
}
@media (max-width: 768px) {
    .footer-links{
        flex-direction: column;
    }
    .social-media-wrap{
        flex-direction: column;
        gap: 10px;
    }
}
